<!-- eslint-disable -->
<template>
  <div class="marcar-consulta-wrapper">
    <div class="marcar-consulta">
      <div class="left lateral">
        <div class="copy">
          Gostaríamos muito que nos soubesse, também, aqui.
          Neste
          <i>espaço</i> que intervala entre o seu contacto e a confiança que espera de nós.
        </div>
      </div>
      <div class="right lateralR" @click="openContactForm()">
        <div class="btn">
          <div class="txt">Marque a sua consulta</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  methods: {
    openContactForm() {
      var contact_form = document.querySelector("#contact-form");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = "1s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");

      $("#contact-form .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    },
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.marcar-consulta-wrapper {
  background-color: $turquiose;
  height: pxToEm(185);
  padding: $pd-desktop;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    height: auto;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  .marcar-consulta {
    max-width: pxToEm(1400);
    margin: auto;
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
      align-items: center;
    }

    .left {
      display: flex;
      align-items: center;

      @media only screen and (max-width: 991px) {
        margin: pxToEm(40) 0;
      }

      .copy {
        font-size: pxToEm(22);
        width: pxToEm(950);
        color: $white;

        @media only screen and (max-width: 991px) {
          text-align: center;
        }

        @media only screen and (max-width: 599px) {
          width: auto;
        }
      }
    }

    .right {
      @media only screen and (max-width: 991px) {
        margin-bottom: pxToEm(40);
      }

      .btn {
        width: pxToEm(335);
        height: pxToEm(65);
        border: pxToEm(1) solid $white;
        border-radius: pxtoEm(4);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.5s;
        cursor: pointer;

        &:hover {
          background-color: $white;

          .txt {
            color: $turquiose;
          }
        }

        .txt {
          color: $white;
          font-size: pxToEm(18);
          text-transform: uppercase;
        }
      }
    }
  }
}
</style>



